export default function SellerDsbCtrl() {
  let vm = this;
  vm.list = [
    { name: '', key: 'img', width: '15%', img: true },
    { name: '', key: 'name', width: '15%' },
    { name: '', key: 'type', width: '20%' },
    { name: '', key: 'stateName', width: '10%' },
    { name: '', key: 'price', width: '10%' },
    { name: '', key: 'sellprice', width: '10%' },
    { name: '', key: 'tradetypeName', width: '10%' },
  ]
  vm.report = (project_id) => {
    $http.post('/order/rshop1/add', {id: project_id}).success(() => {
      location.replace(location.href);
    })
  }
}


