export default function BuyerWcCtrl() {
  let vm = this;
  vm.list = [
    { name: '', key: 'img', width: '20%', img: true },
    { name: '', key: 'name', width: '20%' },
    { name: '', key: 'type', width: '20%' },
    { name: '', key: 'stateName', width: '15%' },
    { name: '', key: 'totalFee', width: '15%' },
    { name: '', key: 'tradetypeName', width: '10%' },
  ]
}


