export default function ScCtrl() {
  var vm = this;
  vm.list = [
    { name: '', key: 'img', width: '20%', img: true },
    { name: '', key: 'name', width: '20%' },
    { name: '', key: 'pcodeName', width: '15%' },
    { name: '', key: 'type', width: '20%' },
    { name: '', key: 'price', width: '15%' },
  ]
}


