export default function BuyerQrCtrl($http) {
  let vm = this;
  vm.list = [
    { name: '', key: 'img', width: '15%', img: true },
    { name: '', key: 'name', width: '15%' },
    { name: '', key: 'type', width: '20%' },
    { name: '', key: 'stateName', width: '15%' },
    { name: '', key: 'totalFee', width: '15%' },
    { name: '', key: 'tradetypeName', width: '10%' },
  ]
  vm.confirm = (id) => {
    $http.post('/sys/rshop/OrderBuyer/CertificateConfirm', {id: id}).success(() => {
      location.replace(location.href);
    })
  }
  
}


