export default function MainCtrl() {
  var vm = this;
  //
  vm.routes_c = [
    {
      url: 'sc',
      text: '',
      active: false
    },
    {
      url: 'buyer.all',
      text: '',
      active: false
    }
  ];
  //
  vm.routes_p = [
    {
      url: 'sc',
      text: '',
      active: false
    },
    {
      url: 'buyer.all',
      text: '',
      active: false
    }
  ];
  
  vm.routes_spe = [
    {
      url: 'fbsb',
      text: '',
      active: false
    },
    {
      url: 'splb.all',
      text: '',
      active: false
    },
    {
      url: 'seller.all',
      text: '',
      active: false
    }
  ]
  
  let cookies = {}
  document.cookie.split('; ').forEach(item => {
    let arr = item.split('=')
    cookies[arr[0]] = arr[1]
  })
  if (cookies.accountType) {
    vm.accountType = cookies.accountType
    vm.logintype = cookies.logintype
  }
  if(vm.accountType === '3') {
    vm.routes_c = vm.routes_c.concat(vm.routes_spe)
    vm.routes_p = vm.routes_p.concat(vm.routes_spe)
  }
  
  vm.routes = {
    title: '',
    items: vm.logintype == 1 ? vm.routes_p :vm.routes_c
  };
  
  // vm.routes = {
  //   title: '',
  //   items: vm.routes_p.concat(vm.routes_c)
  // };
  
}


